import './App.css';
import Container from 'react-bootstrap/Container';
import Header from './Components/header';
import {Route, Routes, useNavigate} from 'react-router-dom'
import Home from './Components/home';
import { useState, useEffect } from 'react';
import { Button , ToggleButton, ToggleButtonGroup} from 'react-bootstrap';
import Background from './Components/background';
import BackgroundWhite from './Components/backgroundWhite';
import face from './Images/face.png'
import React from 'react';
import { useContext } from 'react';



function App() {

  const[theme, setTheme] = useState('black')  
  const[comp, setComp] = useState(1)  

  const navigate = useNavigate();
  const toggleLight = () => {
    if (theme === 'black'){
      setTheme('white');
    }
  };

  const toggleDark = () => {
    if (theme === 'white') {
      setTheme('black');
    } 
  };

  useEffect(() => {
    document.body.className = theme;
  }, [theme]);
  
  useEffect(() => {
  }, [comp]);


  

  return (
    <div className='theme' style={{width:  '100vw' ,top: '0px', right: '0px', bottom: '0px', left: '0px',}}>
    {theme === 'white'? <BackgroundWhite/>  : <Background/>}
    <Container>
    
    <Header
    theme={theme}
    />

    {comp===1?
    <Home/>
    : <br/>}
  

    </Container>
    <ToggleButtonGroup 
        type="radio" 
        name="options" 
        defaultValue={'white'}
        style={{position:'sticky',  left: '50%', width: '50px', marginLeft: '-25px'}}>
                    <ToggleButton id="tbg-radio-1" value={'white'} style={{height: '40px'}} onClick={toggleLight}>
                      ☼
                    </ToggleButton>
                    <ToggleButton id="tbg-radio-2" variant='outline-dark' value={'black'} onClick={toggleDark}>
                    ☾
                    </ToggleButton>
      </ToggleButtonGroup>
    </div>
    
  );
}

export default App;
